.slider {
    width: 800px;
    margin: 10px auto 0px;
    justify-content: center;
    text-align: center;
    display: flex;
}
.slider__img {
    object-fit: contain;
    width: 640px;
    height: 360px;
}
.slider__dots {
    text-align: center;
    z-index: 2;
    position: relative;
    bottom: 55px;
    color: black;
}
.slider__dot {
    background-color: white;
    opacity: 0.3;
    width: 30px;
    height: 10px;
    border: 1px solid;
    border-radius: 5px;
    margin-left: 5px;
    margin-right: 5px;
    transition: all 1s;
}
.slider__dot:hover {
    opacity: 1;
}
.slider__button {
    opacity: 0.3;
    background-color: white;
    border: 0px white;
    padding: 0;
    transition: opacity 1s;
}
.slider__button:hover {
    opacity: 1;
}

@media(max-width: 600px) {
    .slider__button {
        display: none;
    }
    .slider {
        width: 100%;
    }
    .slider__img {
        object-fit: contain;
        width: 100%;
        height: 100%;
    }
    .slider__dots {
        bottom: 30px;
    }
}
@media(max-width: 300px) {
    .slider__dots {
        bottom: 20px;
    }
}